#root {
  height: 100%;
  margin: auto;
}

html, body {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: -moz-none;
      -ms-user-select: none;
          user-select: none;
  -webkit-user-drag: none;
  margin: 0;
  cursor: url('https://i.ibb.co/7KKz6n3/cursor.webp') 25 25, auto;
}

body::-webkit-scrollbar {
  width: 14px !important;
}

body::-webkit-scrollbar-track {
  background: #232323 !important;
}

body::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 2px solid transparent;
  background-clip: content-box;
  background-color: #F279A6 !important;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #111;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 1000;
  transition: opacity 1s cubic-bezier(0, 0.43, 0, 0.99);
  opacity: 1;
}

.loading img {
  max-width: 400px;
  object-fit: contain;
  transition: all 2s cubic-bezier(0, 0.43, 0, 0.99);
  transform: scale(0.1);
}

.golapi-swiper-coverflow {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 1000px;
}

.golapi-swiper-coverflow .swiper-slide {
  margin: auto;
}

.golapi-swiper-coverflow * {
  border-radius: 0.5em;
}

.golapi-swiper-fade {
  background: #111;
  position: fixed;
  top: 0;
  left: 0;
}

.golapi-swiper-fade, .golapi-swiper-fade * {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  object-position: center;
}


.golapi-swiper-fade img {
  filter: blur(5px) brightness(0.5) grayscale(0.75);
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 90%;
  height: fit-content;
}

.swiper-slide img {
  display: block;
  width: 100%;
}

@media (min-width: 768px) { 
  .swiper-slide {
    width: 400px;
  }  
}

.logo-section {
  max-width: 700px;
  margin-top: auto;
}

.golapi-slide-text {
  color: white;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1em;

}

.golapi-slide-text span {
  color: lightgray;
  font-weight: bold;
  font-size: 0.7em;
}

.nav {
  position: relative;
  display: flex;
  z-index: 100;
  width: 90%;
  margin: auto;
  height: 70px;
  justify-content: space-evenly;
  align-items: center;
}

.nav p {
  color: white;
  text-transform: uppercase;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  text-shadow: 0 3px 20px black;
}

.footer {
  position: relative;
  z-index: 100;
  width: 90%;
  margin: auto;
  height: 70px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.footer h1 {
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: medium;
}

.footer img {
  width: 80px;
  height: 80px;
}

.footer-section {
  display: flex;
  align-items: center;
  justify-content: center;
  color: aliceblue;
}

.footer-section a {
  color: #eb78a3;
}
